import { Link } from "react-router-dom";
import { LazyLoadImage, trackWindowScroll } from 'react-lazy-load-image-component';
import list from './js/listHome'

const Home = () => {

    return (
        <section className="container">
            <h1 className="mainTitle text-center overflow-hidden display-1 text-dark">Catalogue</h1>

            <div className="row row-cols-1 row-cols-md-2 d-flex justify-content-center overflow-visible mb-2">
                {list.map((props, index) => {
                    return (
                        <Link to={"/" + props.link} className={`m-2 col-10 col-sm-8 col-md-5 col-lg-4 position-relative rounded background d-flex align-items-center justify-content-center scaleOnHover p-0 overflow-hidden boxshadow`} id={props.id} key={index} style={{ minHeight: '10rem', maxHeight: '17.5rem' }}>
                            <LazyLoadImage
                                style={{
                                    filter: 'brightness(90%)',
                                    objectFit: 'cover'
                                }}
                                width={'100%'}
                                alt={props.id + 'Image'}
                                className="overflow-hidden"
                                scrollPosition={props.scrollPosition}
                                effect="blur"
                                src={index <= 4 ? `/home/${props.link}.webp` : `/placeholder.png`}
                            />
                            <h3 className="position-absolute h4 text-white text-center mx-2" style={{ zIndex: '10', fontWeight: '400', textTransform: 'uppercase' }}>{props.name}</h3>
                            <div className='position-absolute bg-dark container top-0 start-0 bg-opacity-50' style={{ height: '100%' }}>
                            </div>
                        </Link>
                    )
                })}
            </div>
        </section>
    )
}

export default trackWindowScroll(Home);